<template>
<div>
  <v-container>
    <custom-snackbar ref="customSnackbar"></custom-snackbar>
    <v-card>
      <v-card-title class="headline font-weight-regular blue-grey white--text">Audit Record</v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-container fluid>
            <v-subheader>Audit Details</v-subheader>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-flex xs12 md2 class="pr-1">
                <v-text-field v-model="audit.id" label="Audit ID" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audit.auditor" label="Auditor" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-menu
                  ref="menuDatePicker"
                  :close-on-content-click="false"
                  v-model="menuDatePicker"
                  :nudge-right="40"
                  :return-value.sync="audit.audit_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audit.audit_date"
                    label="Audit Date"
                    prepend-icon="event"
                    readonly
                    data-vv-name="Audit Date" 
                    :error-messages="errors.collect('Audit Date')"                     
                  ></v-text-field>
                  <!-- v-validate = "{rules: { required: true }}" -->
                  <v-date-picker v-model="audit.audit_date" @input="$refs.menuDatePicker.save(audit.audit_date)"></v-date-picker>
                </v-menu>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md2 class="pr-1">
                <v-text-field v-model="audit.physician_name" label="MD Name" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audit.npi" label="MD ID#/NPI" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audit.facility" label="Facility" :disabled="!newRecord"></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md2 class="pr-1">
                <v-text-field v-model="audit.patient_name" label="Patient Name" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audit.dob" label="Date of Birth" :disabled="!newRecord"></v-text-field>
              </v-flex>
              <v-flex xs12 md5 class="pr-1">
                <v-text-field v-model="audit.service_date" label="Date of Service" :disabled="!newRecord"></v-text-field>
              </v-flex>
            </v-layout> 
            <v-layout row wrap>
              <v-flex xs12 md6 class="pr-1">
                <!-- <v-text-field v-model="audit.icd10_billed" label="ICD10 Codes Billed" :disabled="!newRecord"></v-text-field> -->
                <v-combobox
                  v-model="audit.icd10_billed"
                  label="ICD10 Codes Billed"
                  chips
                  clearable
                  multiple
                  :disabled="!newRecord"
                >
                  <template slot="selection" slot-scope="data">
                    <v-chip :selected="audit.icd10_billed.selected" close @input="removeChip(audit['icd10_billed'], data.item)">
                      <strong>{{ data.item }}</strong>&nbsp;
                    </v-chip>
                  </template>
                </v-combobox>
              </v-flex>
              <v-flex xs12 md6 class="pr-1">
                <!-- <v-text-field v-model="audit.icd10_audited" label="ICD10 Codes Audited" data-vv-name="ICD10 Codes Audited" v-validate="'required'" required :error-messages="errors.collect('ICD10 Codes Audited')"></v-text-field> -->
                <v-combobox
                  v-model="audit.icd10_audited"
                  label="ICD10 Codes Audited"
                  chips
                  clearable
                  multiple
                >
                  <template slot="selection" slot-scope="data">
                    <v-chip :selected="audit.icd10_audited.selected" close @input="removeChip(audit['icd10_audited'], data.item)">
                      <strong>{{ data.item }}</strong>&nbsp;
                    </v-chip>
                  </template>
                </v-combobox>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md6 class="pr-1">
                <v-select v-model="audit.audit_type" :items="auditTypes" label="Type of Audit"></v-select>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md6 class="pr-1">
                <v-menu
                  ref="menuDatePickerChargeCorrection"
                  :close-on-content-click="false"
                  v-model="menuDatePickerChargeCorrection"
                  :nudge-right="40"
                  :return-value.sync="audit.charge_correction_sent_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audit.charge_correction_sent_date"
                    label="Charge Correction Sent Date"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker v-model="audit.charge_correction_sent_date" @input="$refs.menuDatePickerChargeCorrection.save(audit.charge_correction_sent_date)"></v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12 md6 class="pr-1">
                <v-menu
                  ref="menuDatePickerAuditResults"
                  :close-on-content-click="false"
                  v-model="menuDatePickerAuditResults"
                  :nudge-right="40"
                  :return-value.sync="audit.audit_results_communicated_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audit.audit_results_communicated_date"
                    label="Audit Results Communicated Date"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker v-model="audit.audit_results_communicated_date" @input="$refs.menuDatePickerAuditResults.save(audit.audit_results_communicated_date)"></v-date-picker>
                </v-menu>
              </v-flex>
            </v-layout>
            <v-layout row wrap>
              <v-flex xs12 md6 class="pr-1">
                <v-menu
                  ref="menuDatePickerAuditComplete"
                  :close-on-content-click="false"
                  v-model="menuDatePickerAuditComplete"
                  :nudge-right="40"
                  :return-value.sync="audit.audit_completion_date"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
                >
                  <v-text-field
                    slot="activator"
                    v-model="audit.audit_completion_date"
                    label="Audit Completed Date"
                    prepend-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker v-model="audit.audit_completion_date" @input="$refs.menuDatePickerAuditComplete.save(audit.audit_completion_date)"></v-date-picker>
                </v-menu>
              </v-flex>
              <v-flex xs12 md6 class="pr-1">
                <v-text-field v-model="audit.audit_score" label="Audit Score"></v-text-field>
              </v-flex>
            </v-layout>
            <v-subheader>Coding Details</v-subheader>
            <v-divider></v-divider>
            <div v-for="(detail, index) in audit.details" :key="detail.audit_details_id" >
              <v-layout row wrap>
                <v-flex xs12 md2 class="pr-1">
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.cpt_billed" label="CPT Code Billed" :disabled="!newRecord"></v-text-field>          
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.cpt_audited" label="CPT Code Audited"></v-text-field>          
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.modifier_billed" label="Modifier Billed" :disabled="!newRecord"></v-text-field>          
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.modifier_audited" label="Modifier Audited"></v-text-field>          
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.units_billed" label="Units Billed" :disabled="!newRecord"></v-text-field>          
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.units_audited" label="Units Audited"></v-text-field>          
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-radio-group v-model="detail.pass" @change="testPassFail(index)">
                    <!-- @change="testPassFail(index)" -->
                    <v-radio label="Pass - No action needed" v-bind:value="true"></v-radio>
                    <v-radio label="Fail - Action Required" v-bind:value="false"></v-radio>
                  </v-radio-group>
                </v-flex>
                <v-flex xs12 md2 class="pr-1">
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-select v-model="detail.auditFailReason" :items="failReasons" label="Fail Reason" v-if="!detail.pass" ></v-select>
                      <!-- @change="testFailReason(index)" -->
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap>
                    <v-flex xs12>
                      <v-text-field v-model="detail.auditFailReasonOther" label="Other Reason" v-if="detail.auditFailReason === 'Other'"></v-text-field>          
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-flex xs12 md2>
                  <v-textarea v-model="detail.comments" box label="Comments" auto-grow></v-textarea>
                </v-flex>
              </v-layout>
            </div>
          </v-container>
          <v-btn @click="saveAudit()">Save</v-btn>
          <v-btn @click="cancelAudit()">Cancel</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
  <confirmation-dialog ref="confirmationDialog" title="Confirm" message="Are you sure you want to cancel your changes?"></confirmation-dialog>
</div>
</template>

<script>
import Axios from "axios";
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";

export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar
  },
  data: () => ({
    menuDatePicker: null,
    menuDatePickerChargeCorrection: null,
    menuDatePickerAuditResults: null,
    menuDatePickerAuditComplete: null,
    audit: {
      audit_date: ""
    }
  }),
  mounted() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    this.init();
  },
  computed: {
    newRecord() {
      return false;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(row => row.keyword === "AUDIT_TYPE").map(row => row.value);
    },
    failReasons() {
      return this.$store.getters.APPLICATION_LISTS.filter(row => row.keyword === "AUDIT_FAIL_REASON").map(row => row.value);
    }
  },
  methods: {
    async init() {
      const tmpResult = await Axios.get(Config.apiRoot + "/audit/" + this.$route.params.audit_id);
      this.audit = tmpResult.data[0];
      this.audit.icd10_billed = this.audit.icd10_billed.split(","); // fix arrays
      this.audit.icd10_audited = this.audit.icd10_audited.split(",");
    },
    removeChip(obj, item) {
      obj.splice(obj.indexOf(item), 1);
      obj = [...obj];
    },
    saveAudit: function() {
      this.$validator.validateAll().then(async result => {
        if (!result) {
          return;
        } else {
          const tmpResult = await Axios.put(Config.apiRoot + "/audit/" + this.audit.id, this.audit);
          console.log("tmpResult - ", tmpResult);
          if (tmpResult.data.success) {
            this.showMessage("success", "Record sucessfully saved");
          } else {
            this.showMessage("error", "There was a problem saving your changes.  MSG - " + tmpResult.data.message);
          }
        }
      });
    },
    cancelAudit: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    testPassFail: function(index) {
      if (this.audit.details[index].pass) {
        this.audit.details[index].auditFailReason = "";
        this.audit.details[index].auditFailReasonOther = "";
      }
    },
    testFailReason: function(index) {
      // TODO - not working correctly.  causes field to not be updated for a couple of clicks on fields.
      if (this.audit.details[index].auditFailReason != "Other") {
        this.audit.details[index].auditFailReasonOther = "";
      }
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
.v-subheader-donotuse {
  background-color: lightblue;
}
</style>
